const ComboField: React.FC<IComboFieldProps & IComboMainFieldProps & IComboContainerProps> = (
	props: IComboFieldProps & IComboMainFieldProps & IComboContainerProps,
): JSX.Element => {
	const comboClasses = comboStyles();
	const comboSelectClasses = comboSelectStyles();

	let id: string | undefined;
	let label: React.ReactNode;
	let textfieldProps: any;
	let items: any;
	let fieldName: string | undefined;
	let selectName: string | undefined;
	let defaultValue: string;
	let fieldValue:  string | undefined;
	let selectValue:  string | undefined;
	let type: string;
	let fieldOnChange: any;
	let selectOnChange: any;
	let disableSelect: boolean | undefined;
	let inputProps: any;
	let currency : boolean;
  	//const [blankValue, setBlankValue] = React.useState(false);
	const {noDefault} = props;
	const {} = props;
	({ id, label, items, fieldName, selectName, fieldOnChange, selectOnChange, currency,
		type, fieldValue, selectValue, defaultValue, disableSelect, inputProps, ...textfieldProps } = props);
	//const numallowedpattern = new RegExp(/\D/);
	//const givmin = {min:'0'};

	if (inputProps === undefined) {
		inputProps = {};
	}

	if (noDefault) {
		defaultValue = 'none';
	}


	let value: any = '';

	if (fieldValue) {
		if (currency) {
			value = formatNumber(fieldValue);
		} else {
			value = fieldValue;
		}
	} else {
		if (type==='tel') {
			value = '0'
		} else if (type ==='number') {
			value = '';
		} else {
			value = ''
		}
	}

	let endadornment:any = null;
	if(items){
		if(items.length === 1){
			endadornment = (
				<OutlinedInput
					classes={comboSelectClasses}
					name={selectName}
					disabled={true}
					value={items[0].name}
					labelWidth={0}
					id={id + '_adornment'} />
			);
		}
		else{
			endadornment = (
				<Select
					value={selectValue ? selectValue : defaultValue}
					onChange={
						(e) => { 
									selectOnChange(e);
								}
						}
					disabled={disableSelect ? disableSelect : false}
					input={
						<OutlinedInput
							classes={comboSelectClasses}
							name={selectName}
							labelWidth={0}
							id={id + '_adornment'}
						/>
					}
				>
					{noDefault ? <MenuItem selected disabled value="none">
							
					</MenuItem> : null}
					{items.map((item: any) => (
						<MenuItem key={item.value} value={item.value}>
							{item.name}
						</MenuItem>
					))}
				</Select>
			);
		}
	}
	else{
		endadornment = '';
	}

	return (
		<React.Fragment>
			<InputLabel htmlFor={id + '_label'}>{label}</InputLabel>
			<OutlinedInput
				id={id}
				classes={comboClasses}
				type={currency ? 'tel' : type === 'number' ? 'tel' : type}
				name={fieldName}
				value={value}
				onChange={(e) => {
					/*if (blankValue) {
						setBlankValue(false);
					}*/
					if (fieldOnChange) {
						fieldOnChange(e);
					}
				}}
		        onFocus={() => {
		          /*if (((type==='tel' || type==='number')) && (value === 0 || value === '0')) {
		            setBlankValue(true);
		          }*/
		        }}
	        	onBlur={() => {
		          /*if ((type==='tel' || type==='number')) {
		            setBlankValue(false);
		          }*/
		        }}
				inputProps={inputProps}
				endAdornment = {(
					<InputAdornment position="end">
						{ endadornment }
					</InputAdornment>
				)}
				{...textfieldProps}
			/>
		</React.Fragment>
	);
};
