import React from 'react';
import { 
    Grid, 
    Typography,
    Box, 
} from '@material-ui/core/';
import { Link } from 'react-router-dom';
import { 
    makeStyles,
} from '@material-ui/core/styles';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { 
    faEdit,
} from '@fortawesome/free-solid-svg-icons';

/**
 * text field items
 */

const hmotextitemStyles = makeStyles({
    hmoname: {
      fontSize: '0.75rem',
      color: 'rgba(39, 46, 76, 0.6)',
      textTransform: 'uppercase',
      paddingBottom: 4,
    },
    hmovalue: {
        fontSize: '0.875rem',
        color: 'rgba(39, 46, 76, 1)',
        fontWeight: 600,
    },
    hmovaluelink: {
        fontSize: '0.875rem',
        color: 'rgba(39, 46, 76, 1)',
        textDecoration: 'underline',
        fontWeight: 600,
    },
});

interface HMOTextItemProps {
    name: string;
    value: string;
    link?: string;
    rightBtnLink?: string;
}

export const HMOTextItem: React.FC<HMOTextItemProps> = (
	props: HMOTextItemProps,
): JSX.Element => {

    const classes = hmotextitemStyles(props);

    return(
        <Grid container alignItems="flex-start" style={{paddingTop: 8, paddingBottom: 8, paddingRight: 16}}>
            <Grid item xs={12}>
                <Typography className={classes.hmoname}>{props.name}</Typography>
            </Grid>
            <Grid item xs={12}>
                <Box display="flex" flexDirection="row" alignItems="center">
                    {
                        props.link ? 
                        (
                            <Link to={props.link} className={classes.hmovaluelink}>
                                {props.value}
                            </Link>
                        ) : 
                        (
                            <Typography className={classes.hmovalue}>{props.value}</Typography>
                        )
                    }
                    {
                        props.rightBtnLink ?
                        (
                            <Link to={props.rightBtnLink}>
                                <FontAwesomeIcon
                                    icon={faEdit}
                                    style={{ color: '#3AB77D', marginLeft: 8 }} />
                            </Link>
                        ) : ''
                    }
                </Box>
            </Grid>
        </Grid>
    );
}

HMOTextItem.defaultProps = {
    name: '',
    value: '',
    link: '',
    rightBtnLink: '',
}