import * as React from 'react';
import { useState } from 'react';
import Grid from '@material-ui/core/Grid';
import Divider from '@material-ui/core/Divider';
import Popover from '@material-ui/core/Popover';
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import { PlanTypeTabBar } from 'Pages/CreateProposal/Components/PlanTypeTabBar';
import { CategoryForm } from 'Pages/CreateProposal/Components/CategoryForm';
import { PackagesSidebarList } from 'Pages/CreateProposal/Components/PackagesSidebarList';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { LimitTypeItems } from 'Components/UI/TableComponent/DropdownConstants';
import { GlobalFunction } from 'Components/UI/GlobalFunction';

const addLinkStyles = makeStyles((theme: Theme) =>
	createStyles({
		root: {
			maxWidth: 360,
			backgroundColor: theme.palette.background.paper,
		},
	}),
);

interface CBPlanTypesProps {
	benefitNodes: any[];
	planTypeList: any[];
	contractNode: any;
	categoryNode: any;
	roomAndBoardList: any[];
	coverageList: any[];
	riderList: any[];
	rows: any[];
	selectedPlanType: string;
	selectedPlanPackage: string;
	selectedBenefitCategory: string;
	classes: any;
	submitMode?: boolean;
	SetBenefitNodes: any;
	SetSelectedPlanType: any;
	SetSelectedPlanPackage: any;
	SetSelectedBenefitCategory: any;
	SetNewNodeCounter: any;
	viewOnly?: boolean;
	newNodeCounter: number;
	editMode?: boolean;
	editedNodeList?: any[],
	SetEditedNodeList?: any,
}

export const CBPlanTypes: React.FC<CBPlanTypesProps> = (props: CBPlanTypesProps): JSX.Element => {
	const { 
		benefitNodes,
		categoryNode,
		rows,
		planTypeList, 
		roomAndBoardList, 
		coverageList, 
		riderList, 
		selectedPlanType,
		selectedPlanPackage,
		selectedBenefitCategory,
		SetBenefitNodes, 
		SetSelectedPlanType, 
		SetSelectedPlanPackage, 
		SetSelectedBenefitCategory,
		SetNewNodeCounter,
		editMode,
		viewOnly,
		newNodeCounter,
		SetEditedNodeList,
		editedNodeList,
    } = props;
    
	const [anchorEl, setAnchorEl] = useState<HTMLButtonElement | null>(null);
	const [popoverList, setPopoverList] = useState<any[]>([]);
	const popoverOpen = Boolean(anchorEl);
	
	const addLinkClasses = addLinkStyles();
	
	const handleChangeSubForm = (e: any, field: string = 'custom_metadata', fieldIndex: number = -1) => {
		if(e.target.name === 'mobile_ape_availees')
		{
			e.target.value.replace('-','');
			e.target.value.replace('.','');
			if(e.target.value.indexOf('0')===0 && e.target.value.length>0) 
			{
				e.target.value = e.target.value.substring(1);
			}
		}
		handleChangeSubForm2(e.target.name, e.target.value, field, fieldIndex);
	}

	const handleChangeSubForm2 = (name: string, value: any, field: string = 'custom_metadata', fieldIndex: number = -1) => {
		let nodes = [...benefitNodes];
		let editedNodes: any[] = [];

		if (editMode && editedNodeList) {
			editedNodes = [...editedNodeList];
		}

		let index = 0;

		index = nodes.findIndex(item => 
		  item.id === categoryNode.id &&
	      item.code === categoryNode.code &&
		  item.level === categoryNode.level && 
	      item.tree_path[2] === categoryNode.tree_path[2] && 
	      item.tree_path[3] === categoryNode.tree_path[3]
	    );

		let data: any = {};

		if (field === 'limits' && fieldIndex === -1) {
			data = value;
		} else {
			data[name] = value;
		}

		if (nodes[index][field]) {
			if (fieldIndex > -1) {
				nodes[index][field][fieldIndex] = Object.assign ({}, nodes[index][field][fieldIndex], data);
			} else {
				if (Array.isArray(data)) {
					nodes[index][field] = [...data];
				} else {
					nodes[index][field] = Object.assign ({}, nodes[index][field], data);
				}
			}

			if (editMode && nodes[index]['original_id']) {
				const id = nodes[index]['id'];

				const found = editedNodes.find ((tree) => {
					return id === tree[tree.length-1].id;
				});

				if (!found) {
					const tree = GlobalFunction.getNodeTree(nodes[index], nodes);
					editedNodes.push(tree);	
				} 

				SetEditedNodeList(editedNodes);
			}

			SetBenefitNodes(nodes);
		}
	}

	const createNode = (countParam: number = 0, nodeParam: any, template: any, benefitData: any, parentArray: object[], parentPos: number): [number, any[]] => {
		let count: number = countParam;
		let nodes: any[] = [...nodeParam];

		parentArray.map((parent: any) => {
	    	const newID = "NODE-NEW-"+(newNodeCounter+count).toString().padStart(3, '0');
	    	let newBenefitData = Object.assign({}, benefitData);
			const childLevel: number = nodes[parent.index].level + 1;
	    	
			const newTreePath = [...newBenefitData['tree_path']];

	    	newTreePath[2] = parent['planType'] ? parent['planType'] : selectedPlanType;
	    	newTreePath[parentPos] = parent.code;

	    	newBenefitData['id'] = newID;
			newBenefitData['level'] = childLevel;
			newBenefitData['tree_path'] = newTreePath;

	    	if (childLevel === 5) {
	    		newBenefitData['type'] = 'BenefitCategory';
	    	}
	    	if (childLevel === 6) {
	    		newBenefitData['type'] = 'BenefitType';
	    	}
	    	if (childLevel === 7) {
	    		newBenefitData['type'] = 'Benefit';
	    	}
	    	if (childLevel === 8) {
	    		newBenefitData['type'] = 'Sub-Benefit';
	    	}

			const newNode = Object.assign({}, template, newBenefitData);

			delete newNode.original_children;
			delete newNode.original_id;

			let newChildren = [...nodes[parent.index].children];

			if (!newChildren.includes(newID)) {
				newChildren.push(newID);
			}

			nodes[parent.index]['children'] = newChildren;
			nodes.push(newNode);
			count++;

		});


		return [count, nodes];
	}

	const handleCreateCivilStatus = (name: string = '', description: string = '') => {
		let nodes: any[] = [...benefitNodes];
		let parentArray: object[] = [];
		let templateNodes: object[] = [];
		let planTypes: object[] = [];
		let template: object | null = null;
		let count: number = 0;
		const level: number = 6;
		const childLevel: number = 7;

		if (selectedPlanType === 'BASE') {
			planTypes = planTypeList.reduce((result, item) => {
		    	result.push(item.code);
		    	return result;
		    }, new Array());
		}

		parentArray = nodes.reduce((result, item, i) => {
	      if ( item.level === level &&
		    (planTypes.length > 1 ? 
		    	planTypes.includes(item.tree_path[2]) : 
		    	item.tree_path[2] === selectedPlanType) &&
	        item.tree_path[3] === selectedPlanPackage &&
	        item.tree_path[4] === selectedBenefitCategory 
	      ){
	      	const input = {
	      		index: i,
	      		code: item.code,
	      		planType: item.tree_path[2]
	      	};

	      	result.push(input);
	      }
	      return result;
	    }, new Array());

	    templateNodes = nodes.reduce((result, item) => {
	      if ( item.level === childLevel &&
	        item.tree_path[2] === selectedPlanType  &&
	        item.tree_path[3] === selectedPlanPackage &&
	        item.tree_path[4] === selectedBenefitCategory
	      ){
	      	result.push(item);
	      }
	      return result;
	    }, new Array());

	    if (templateNodes.length > 0) {
	    	template = templateNodes[0];
	    }

	    if (template !== null) {
			const benefitData = {
			    name: name,
			    code: name.replace(/[^A-Z]/g, ''),
			    description: description,
			    metadata: {},
			    metadata_schema: {},
			    included: true,
			    children: [],
			    custom_metadata: Object.assign({title: name}),
			    custom_metadata_schema: {
			      properties: {
			        title: {type: 'string'},
			      },
			      required: [],
			    },
	    		tree_path: [
	    			"MCT",
	    			"CT",
	    			'',
	    			selectedPlanPackage,
	    			selectedBenefitCategory,
	    			"",
	    			name.replace(/[^A-Z]/g, '')
	    		],
			 };

			[count, nodes] = createNode(0, nodes, template, benefitData, parentArray, 5);

			SetBenefitNodes(nodes);
			SetNewNodeCounter(newNodeCounter + count);
		}
	}

	const handleCreateRelationship = (name: string = '', description: string = '', data: object = {}, parent: any | null = null) => {
		let nodes: any[] = [...benefitNodes];
		let parentArray: object[] = [];
		let planTypes: object[] = [];
		let templateNodes: object[] = [];
		let template: object | null = null;
		let count: number = 0;

		const level: number = parent.level;
		const childLevel: number = level + 1;

		if (selectedPlanType === 'BASE') {
			planTypes = planTypeList.reduce((result, item) => {
		    	result.push(item.code);
		    	return result;
		    }, new Array());
		}

		parentArray = nodes.reduce((result, item, i) => {
	      if ( item.level === level && 
				item.name === parent.name &&
				item.code === parent.code &&
			    (planTypes.length > 1 ? 
			    	planTypes.includes(item.tree_path[2]) : 
			    	item.tree_path[2] === parent.tree_path[2]) &&
		        item.tree_path[3] === parent.tree_path[3] &&
		        item.tree_path[4] === parent.tree_path[4] &&
		        item.tree_path[5] === parent.tree_path[5] &&
		        item.tree_path[6] === parent.tree_path[6]
	      ){
	      	const input = {
	      		index: i,
	      		code: item.code,
	      		planType: item.tree_path[2]
	      	};

	      	result.push(input);
	      }
	      return result;
	    }, new Array());

	    templateNodes = nodes.reduce((result, item) => {
	      if ( item.level === childLevel &&
	        item.tree_path[2] === selectedPlanType  &&
	        item.tree_path[3] === selectedPlanPackage &&
	        item.tree_path[4] === selectedBenefitCategory
	      ){
	      	result.push(item);
	      }
	      return result;
	    }, new Array());

	    if (templateNodes.length > 0) {
	    	template = templateNodes[0];
	    }

	    if (template !== null) {
			const benefitData = {
			    name: name,
			    code: name.replace(/[^A-Z]/g, ''),
			    description: description,
			    metadata: {},
			    metadata_schema: {},
			    included: true,
			    children: [],
			    custom_metadata: Object.assign({title: name}, data),
			    custom_metadata_schema: {
			      properties: {
			        title: {type: 'string'},
			      },
			      required: [],
			    },
	    		tree_path: [
	    			"MCT",
	    			"CT",
	    			'',
	    			selectedPlanPackage,
	    			selectedBenefitCategory,
	    			'DP',
	    			'',
	    			name.replace(/\s+/g, '').toUpperCase()
	    		],
			 };

			[count, nodes] = createNode(0, nodes, template, benefitData, parentArray, 6);

			SetBenefitNodes(nodes);
			SetNewNodeCounter(newNodeCounter + count);
		}
	}

	const handleChangeRowMetadata = (e: any, id: number) => {
		handleChangeRowMetadata2(e.target.name, e.target.value, id);
	}

	const handleChangeRowMetadata2 = (name: string, value: any, id: number, metaIndex: number = -1, metaName: string = '') => {
		let nodes = [...benefitNodes];
		let editedNodes: any[] = [];

		if (editMode &&  editedNodeList) {
			editedNodes = [...editedNodeList];
		}

		const index = nodes.findIndex(item => item.id === id);

		let metadata = {};	

		if (metaIndex > -1 && metaName) {
			metadata = Object.assign({}, nodes[index]['custom_metadata']);
			if (metadata[name]) {
				metadata[name][metaIndex][metaName] = value;
			}
		} else {
			metadata[name] = value;
		}

		if (nodes[index]['custom_metadata']) {
			if (selectedPlanType !== 'BASE') {
				nodes[index]['custom_metadata'] = Object.assign ({}, nodes[index]['custom_metadata'], metadata);
				nodes[index]['edited'] = true;
			} else {
				nodes[index]['custom_metadata'] = Object.assign ({}, nodes[index]['custom_metadata'], metadata);
				const baseNode = nodes[index];
				planTypeList.map ((plan: any) => {
					if (plan.code !== 'BASE') {
						const planIndex = GlobalFunction.getMatchByPlanCode(nodes, baseNode, plan.code);

						if (nodes[planIndex]['edited'] === false) {
							nodes[planIndex]['custom_metadata'] = Object.assign ({}, nodes[planIndex]['custom_metadata'], metadata);
						}
					}
				});
			}

			if (editMode && nodes[index]['original_id']) {
				const id = nodes[index]['id'];

				const found = editedNodes.find ((tree) => {
					return id === tree[tree.length-1].id;
				});

				if (!found) {
					const tree = GlobalFunction.getNodeTree(nodes[index], nodes);
					editedNodes.push(tree);	
				} 

				SetEditedNodeList(editedNodes);
			}
		}

		SetBenefitNodes(nodes);
	}

	const handleChangeRowLimits = (e: any, id: number, i: number) => {
		let nodes = [...benefitNodes];
		let editedNodes: any[] = [];

		if (editMode && editedNodeList) {
			editedNodes = [...editedNodeList];
		}

		const index = nodes.findIndex(item => 
			item.id === id
		);

		const limValue = e.target.value;
		const limName = e.target.name;
		
		let limits = [...nodes[index]['limits']];

		if (nodes[index]['limits']) {
			if (limName === 'limit_type') {
				const newLimits = limValue.reduce((result, val) => {
					const limType = LimitTypeItems.find((limType) => limType.label === val);

					if (limType) {
						const found = limits.find((item) => item.type === limType.type && item.limit_unit === limType.limit_unit);

						if (found) {
							result.push(found);
						} else {
							const input = {
								type: limType.type,
								limit: 0,
								limit_unit: limType.limit_unit,
								daily_limit: 0,
							};

							result.push(input);	
						}
					} 

					return result;
				}, new Array());

				limits = [...newLimits];
			} else if (['limit','daily_limit'].includes(limName)) {
				let newLim = Object.assign({}, limits[i]);
				newLim[limName] = parseFloat(limValue);
				limits[i] = newLim;
			}

			if (selectedPlanType !== 'BASE') {
				nodes[index]['limits'] = limits;
				nodes[index]['edited'] = true;
			} else {
				nodes[index]['limits'] = limits;
				const baseNode = nodes[index];
				planTypeList.map ((plan: any) => {
					if (plan.code !== 'BASE') {
						const planIndex = GlobalFunction.getMatchByPlanCode(nodes, baseNode, plan.code);

						if (nodes[planIndex]['edited'] === false) {
							nodes[planIndex]['limits'] = limits;
						}
					}
				});
			}

			if (editMode && nodes[index]['original_id']) {
				const id = nodes[index]['id'];

				const found = editedNodes.find ((tree) => {
					return id === tree[tree.length-1].id;
				});

				if (!found) {
					const tree = GlobalFunction.getNodeTree(nodes[index], nodes);
					editedNodes.push(tree);	
				} 

				SetEditedNodeList(editedNodes);
			}

			SetBenefitNodes(nodes);
		}
	}

	const handleChangeRowSelection = (rowObjs: any[]) => {
		let nodes = [...benefitNodes];
		let editedNodes: any[] = [];

		if (editMode && editedNodeList) {
			editedNodes = [...editedNodeList];
		}

		rowObjs.map ((rowObj) => {
			const index = nodes.findIndex(item => 
				item.id === rowObj.id
			);

			if (selectedBenefitCategory === 'EX' && nodes[index]['custom_metadata']) {
				if (nodes[index]['custom_metadata']['is_excluded'] !== undefined && 
					nodes[index]['custom_metadata']['is_excluded'] !== rowObj.included) {
					nodes[index]['custom_metadata']['is_excluded'] = rowObj.included;

					if (editMode && nodes[index]['original_id']) {
						const id = nodes[index]['id'];

						const found = editedNodes.find ((tree) => {
							return id === tree[tree.length-1].id;
						});

						if (!found) {
							const tree = GlobalFunction.getNodeTree(nodes[index], nodes);
							editedNodes.push(tree);	
						} 
					}
				}
			} else {
				if (nodes[index]['included'] !== rowObj.included) {
					// if (editMode) {
						const ancestors = GlobalFunction.getNodeTree(nodes[index], nodes);
						const descendants = GlobalFunction.getDescendants(nodes[index], nodes);
						const path = [...ancestors, ...descendants];

						if (rowObj.included === true) {
							path.map((item) => {
								const index2 = nodes.findIndex((item2) => item.id === item2.id);

								if (index2 > -1) {
									nodes[index2]['included'] = rowObj.included;
								}
							});
						} else if (rowObj.included === false) {
							nodes[index]['included'] = rowObj.included;
							descendants.map((item) => {
								const index2 = nodes.findIndex((item2) => item.id === item2.id);

								if (index2 > -1) {
									nodes[index2]['included'] = rowObj.included;
								}
							});	
						}
					// } else {
						// nodes[index]['included'] = rowObj.included;
					// }
				}
			}
		});

		if (editMode) {
			SetEditedNodeList(editedNodes);
		}
		SetBenefitNodes(nodes);
	}

	const handlePackagePopoverOpen = (
		e: React.MouseEvent<HTMLButtonElement, MouseEvent>,
		planPackage: string,
	) => {
		const currentTarget = e.currentTarget;

		let nodes: any[] = [];
	
		if (planPackage === 'CV') {
			nodes = coverageList.filter((item: any) => {
				return !item.included;
			});
		}

		if (planPackage === 'RI') {
			nodes = riderList.filter((item: any) => {
				return !item.included;
			});
		}

		setPopoverList(nodes);
		setAnchorEl(currentTarget);
    }
    
	const handlePopoverClose = () => {
		setAnchorEl(null);
	}

	const handlePopoverClick = (planType: string, planPackage: string, benefitCategory: string) => {
		addBenefitCategory(planType, planPackage, benefitCategory);
	}

	const deleteBenefitCategory = (category: any) => {
		let nodes = [...benefitNodes];
		console.log(category);
		// const nodePath = category.tree_path;

		const index = nodes.findIndex(item => 
			item.level === 5 && 
			item.code === category['tree_path'][4] &&
			item.tree_path[2] === category['tree_path'][2] && 
			item.tree_path[3] === category['tree_path'][3] && 
			item.tree_path[4] === category['tree_path'][4] 
		);

		const descendants = GlobalFunction.getDescendants(nodes[index], nodes);

		nodes[index]['included'] = false;
		descendants.map((item) => {
			const index2 = nodes.findIndex((item2) => item.id === item2.id);

			if (index2 > -1) {
				nodes[index2]['included'] = false;
			}
        });	
        
		SetBenefitNodes(nodes);
	}

	const addBenefitCategory = (planType: string, planPackage: string, benefitCategory: string) => {
		let nodes = [...benefitNodes];

		const index = nodes.findIndex(item => 
			item.level === 5 && 
			item.code === benefitCategory &&
			item.tree_path[2] === planType && 
			item.tree_path[3] === planPackage && 
			item.tree_path[4] === benefitCategory
		);

		nodes[index]['included'] = true;

		SetBenefitNodes(nodes);
		setAnchorEl(null);
    }
    
	return (
		<div>
			<Grid container>
				<Grid item sm={12}>
					<PlanTypeTabBar
						selectedPlanType={selectedPlanType}
						planTypeList={planTypeList}
						setSelectedPlanType={SetSelectedPlanType}
						setModalOpen={() => {}}
						viewOnly={viewOnly ? viewOnly : false}
						showEditPlanType={()=>{}}
					/>
				</Grid>

				<Grid item sm={12}>
					<Divider variant="middle" />
				</Grid>

				<Grid item sm={12} md={3}>
					<PackagesSidebarList
						selectedBenefitCategory={selectedBenefitCategory}
						roomAndBoardList={roomAndBoardList}
						riderList={riderList}
						coverageList={coverageList}
						setSelectedPlanPackage={SetSelectedPlanPackage}
						setSelectedBenefitCategory={SetSelectedBenefitCategory}
						deleteBenefitCategory={deleteBenefitCategory}
						setPopoverOpen={handlePackagePopoverOpen}
						viewOnly={viewOnly ? viewOnly : false}
					/>
				</Grid>

				<Grid item sm={12} md={9}>
					<CategoryForm
						selectedPlanType={selectedPlanType}
						selectedBenefitCategory={selectedBenefitCategory}
						setRowSelection={!viewOnly ? handleChangeRowSelection : () => {}}
						setRowLimits={!viewOnly ? handleChangeRowLimits : () => {}}
						setRowMetadata={!viewOnly ? handleChangeRowMetadata : () => {}}
						setRowMetadata2={!viewOnly ? handleChangeRowMetadata2 : () => {}}
						setSubForm={!viewOnly ? handleChangeSubForm : () => {}}
						setSubForm2={!viewOnly ? handleChangeSubForm2 : () => {}}
						createCivilStatus={!viewOnly? handleCreateCivilStatus : () => {}}
						createRelationship={!viewOnly? handleCreateRelationship : () => {}}
						subFormNode={categoryNode}
						rows={rows}
						viewOnly={viewOnly ? viewOnly : false}
					/>
				</Grid>

				<Popover
					open={popoverOpen}
					anchorEl={anchorEl}
					onClose={handlePopoverClose}
					anchorOrigin={{
						vertical: 'bottom',
						horizontal: 'left',
					}}
					transformOrigin={{
						vertical: 'top',
						horizontal: 'left',
					}}
				>
					<List classes={addLinkClasses}>
						{popoverList.length > 0 ? (
							popoverList.map((item: any) =>
								item['name'] && item['code'] ? (
									<ListItem
										button
										key={`${item['code']}`}
										onClick={() =>
											handlePopoverClick(
												item['tree_path'][2] !== undefined
													? item['tree_path'][2]
													: null,
												item['tree_path'][3] !== undefined
													? item['tree_path'][3]
													: null,
												item['tree_path'][4] !== undefined
													? item['tree_path'][4]
													: null,
											)
										}
									>
										<ListItemText
											disableTypography={true}
											primary={`${item['name']}`}
										/>
									</ListItem>
								) : null,
							)
						) : (
							<ListItem>
								<ListItemText disableTypography={true} primary={'Cannot add more.'} />
							</ListItem>
						)}
					</List>
				</Popover>
			</Grid>
		</div>
	);
};
