interface EligibilityPopoverProps {
	onClose: any;
	onSubmit: any;
	rows: any[];
}

const CivilStatus: React.FC<EligibilityPopoverProps> = (props: EligibilityPopoverProps): JSX.Element | null => {
	const {onClose, onSubmit} = props;

	const [name, setName] = useState<string>('');
	const [nameError, setNameError] = useState<string>('');
	const [description, setDescription] = useState<string>('');
	const [descriptionError, setDescriptionError] = useState<boolean>(false);
	const reset = () => {
		setName('');
		setNameError('');
		setDescription('');
		setDescriptionError(false);
	};

	const handleClose = () => {
		reset();
		onClose();
	};
	//console.log("add civil status: "+JSON.stringify(props.rows,null,2))
	const handleSubmit = () => {
		let chk = 0;
		for(let x = 0;x<props.rows.length;x++)
		{
			if(props.rows[x]['included'] && props.rows[x]['name'].toUpperCase().trim()===name.toUpperCase().trim())
			{chk=1;}
		}
		if (name.trim().length === 0 || chk === 1) {
			if(chk === 1)
			{setNameError('Name is Already Used');}
			else
			{setNameError('Input Name')}
		}

		if (description.length === 0) {
			setDescriptionError(true);
		}

		if (name.trim().length > 0 && description.length > 0 && chk === 0) {
			reset();
			onSubmit(name, description);
			onClose();
		}
	};

	return (
		<Grid item sm={12} style={{padding: '10px'}}>
			<DialogTitle id="form-dialog-title">Add a Civil Status</DialogTitle>
			<DialogContent>
				<Grid container spacing={2} justify="space-between" alignItems="flex-start">
					<Grid item xs={12}>
						{/* <InputTextField
							label="Name"
							value={name}
							error={nameError.length>0}
							onChange={(value: string) => {
								setName(value);
								setNameError('');
							}}
							errorMessage={nameError}
						/> */}
						<InputSelect
							id="civil-status-select"
							label="Civil Status"
							controlled
							data={[
								{ id: 1, label: 'Married', value: 'Married' },
								{ id: 2, label: 'Widowed', value: 'Widowed' },
								{ id: 3, label: 'Separated', value: 'Separated' },
								{ id: 4, label: 'Divorced', value: 'Divorced' },
								{ id: 5, label: 'Single', value: 'Single' },
								{ id: 6, label: 'Annulled', value: 'Annulled' },
								{ id: 7, label: 'Single parent', value: 'Single parent' }
							]}
							value={name}
							error={nameError.length > 0}
							onChange={(selected: ISelectItem) => {
								setName(selected.label);;
								setNameError('');
							}}
						/>
					</Grid>

					<Grid item xs={12}>
						<InputTextField
							label="Description"
							value={description}
							error={descriptionError}
							onChange={(value: string) => {
								setDescription(value);
								setDescriptionError(value.length === 0);
							}}
							errorMessage="Input Description"
							multiline
						/>
					</Grid>
				</Grid>
			</DialogContent>
			<DialogActions>
				<Grid container spacing={2} justify="center" alignItems="flex-start">
					<Grid item xs={5}>
						<Button 
							variant="outlined" 
							color="default" 
							fullWidth
							onClick={handleClose}
						>
							Cancel
						</Button>
					</Grid>
					<Grid item xs={5}>
						<Button
							variant="contained"
							color="primary"
							fullWidth
							onClick={handleSubmit}
						>
							Add
						</Button>
					</Grid>
				</Grid>
			</DialogActions>
		</Grid>
	);
}

const Relationship: React.FC<EligibilityPopoverProps> = (props: EligibilityPopoverProps): JSX.Element | null => {
	const {onClose, onSubmit, rows} = props;

	const [civilStatus, setCivilStatus] = useState<string>('');
	const [civilStatusError, setCivilStatusError] = useState<boolean>(false);
	const [relationship, setRelationship] = useState<string>('');
	const [relationshipCode, setRelationshipCode] = useState<string>('');
	const [relationshipError, setRelationshipError] = useState<string>('');
	const [type, setType] = useState<string>('');
	const [typeError, setTypeError] = useState<boolean>(false);
	const [hierarchy, setHierarchy] = useState<string>('');
	const [hierarchyError, setHierarchyError] = useState<boolean>(false);
	const [enrollmentPeriod, setEnrollmentPeriod] = useState<string>('');
	const [enrollmentPeriodError, setEnrollmentPeriodError] = useState<boolean>(false);
	const [enrollmentPeriodUnit, setEnrollmentPeriodUnit] = useState<string>('');
	const [enrollmentPeriodUnitError, setEnrollmentPeriodUnitError] = useState<boolean>(false);
	const [effectivityCoverage, setEffectivityCoverage] = useState<string>('');
	const [effectivityCoverageError, setEffectivityCoverageError] = useState<boolean>(false);
	const [effectivityCoverageUnit, setEffectivityCoverageUnit] = useState<string>('');
	const [effectivityCoverageUnitError, setEffectivityCoverageUnitError] = useState<boolean>(false);
	const [ageLimitMin, setAgeLimitMin] = useState<string>('18');
	const [ageLimitMinError, setAgeLimitMinError] = useState<boolean>(false);
	const [ageLimitMinUnit, setAgeLimitMinUnit] = useState<string>('');
	const [ageLimitMinUnitError, setAgeLimitMinUnitError] = useState<boolean>(false);
	const [ageLimitMax, setAgeLimitMax] = useState<string>('65');
	const [ageLimitMaxError, setAgeLimitMaxError] = useState<boolean>(false);
	const [ageLimitMaxUnit, setAgeLimitMaxUnit] = useState<string>('');
	const [ageLimitMaxUnitError, setAgeLimitMaxUnitError] = useState<boolean>(false);
	
	
	const reset = () => {
		setCivilStatus('');
		setCivilStatusError(false);
		setRelationship('');
		setRelationshipCode('');
		setRelationshipError('');
		setType('');
		setTypeError(false);
		setHierarchy('');
		setHierarchyError(false);
		setEnrollmentPeriod('');
		setEnrollmentPeriodError(false);
		setEnrollmentPeriodUnit('');
		setEnrollmentPeriodUnitError(false);
		setEffectivityCoverage('');
		setEffectivityCoverageError(false);
		setEffectivityCoverageUnit('');
		setEffectivityCoverageUnitError(false);
		setAgeLimitMin('18');
		setAgeLimitMinError(false);
		setAgeLimitMinUnit('');
		setAgeLimitMinUnitError(false);
		setAgeLimitMax('65');
		setAgeLimitMaxError(false);
		setAgeLimitMaxUnit('');
		setAgeLimitMaxUnitError(false);
	};

	const handleClose = () => {
		reset();
		onClose();
	};

	const handleSubmit = () => {
		let errors = 0;
		let ind = 0;
		let chk =0;
		if (civilStatus.length === 0) {
			setCivilStatusError(true);
			errors++;
		}
		else
		{
			for(let x=0;x<props.rows.length;x++)
			{
				// console.log('ind'+ind+' '+civilStatus+' '+x+' '+props.rows.length);
				if(civilStatus===props.rows[x]['code'])
				{ind=x};
			}
			for(let x=0;x<props.rows[ind]['child_nodes'].length;x++)
			{
				// console.log(relationship)
				if(!props.rows[ind]['child_nodes'][x]['deleted'] && relationship.toUpperCase().trim()===props.rows[ind]['child_nodes'][x]['name'].toUpperCase().trim())
				{chk = 1}	
			}
		}
		if (type.length === 0) {
			setTypeError(true);
			errors++;
		}

		if (isNaN(Number(hierarchy)) || Number(hierarchy) < 1) {
			setHierarchyError(true);
			errors++;
		}

		if (relationship.trim().length === 0 || chk === 1) {
			if(chk===1)
				{setRelationshipError('Relationship is Already used in Selected Civil Status');}
			else
				{setRelationshipError('Relationship is Required');}
		}
		else
		{
			setRelationshipError('')
		}
		if (enrollmentPeriod.length === 0) {
			setEnrollmentPeriodError(true);
			errors++;
		}

		if (enrollmentPeriodUnit.length === 0) {
			setEnrollmentPeriodUnitError(true);
			errors++;
		}

		if (effectivityCoverage.length === 0) {
			setEffectivityCoverageError(true);
			errors++;
		}

		if (effectivityCoverageUnit.length === 0) {
			setEffectivityCoverageUnitError(true);
			errors++;
		}

		if (isNaN(Number(ageLimitMin)) || Number(ageLimitMin) < 0) {
			setAgeLimitMinError(true);
			errors++;
		}

		if (ageLimitMinUnit.length === 0) {
			setAgeLimitMinUnitError(true);
			errors++;
		}

		if (isNaN(Number(ageLimitMax)) || Number(ageLimitMax) < 1) {
			setAgeLimitMaxError(true);
			errors++;
		}

		if (ageLimitMaxUnit.length === 0) {
			setAgeLimitMaxUnitError(true);
			errors++;
		}

		// if ( civilStatus.length > 0 && 
		// 	 type.length > 0 && 
		// 	 relationship.length > 0 && 
		// 	 enrollmentPeriod.length > 0 && 
		// 	 enrollmentPeriodUnit.length > 0 &&
		// 	 (hierarchy.trim()!=='' && hierarchy !== '0') &&
		// 	 effectivityCoverage.length > 0 &&
		// 	 effectivityCoverageUnit.length > 0 &&
		// 	 ageLimitMin.length > 0 &&
		// 	 ageLimitMinUnit.length > 0 &&
		// 	 ageLimitMax.length > 0 &&
		// 	 ageLimitMaxUnit.length > 0 &&
		// 	 chk === 0) {
		if(errors === 0 && chk === 0){

			const parent = rows.find ((item) => {
				return civilStatus === item.code;
			});

			const data: object = {
				type: type,
				relationship: relationship, 
				hierarchy: hierarchy.length > 0 ? hierarchy : null,
				enrollment_period: enrollmentPeriod,
				enrollment_period_unit: enrollmentPeriodUnit,
				effectivity_coverage: effectivityCoverage,
				effectivity_coverage_unit: effectivityCoverageUnit,
				age_limit_min: ageLimitMin,
				age_limit_min_unit: ageLimitMinUnit,
				age_limit_max: ageLimitMax,
				age_limit_max_unit: ageLimitMaxUnit,
			};

			reset();
			onSubmit(relationship, relationshipCode, "description", data, parent);
			onClose();
		}
	};


	const getCivilStatus = () => {
		const inputTypes: ISelectItem[] = rows.reduce((result, status, i) => {
			if (status['included']) {
				const input = {
					id: i,
					label: status.name,
					value: status.code
				}

				result.push (input);
			}

			return result;
		}, new Array());

		return inputTypes;
	};

	const getRelationships = () => {
		const currentCivilStatus = props.rows.find((row) => row.code === civilStatus);

		if (currentCivilStatus !== undefined) {
			const inputTypes: ISelectItem[] = RelationshipToPrincipalItems.reduce((result, relationship) => {
				const found = currentCivilStatus.child_nodes.find((child) => child.code === relationship.value);

				if (!found) {
					result.push (relationship);
				}

				return result;
			}, new Array());
			return inputTypes;
		} 

		const inputTypes: ISelectItem[] = RelationshipToPrincipalItems;
		
		return inputTypes;
	};

	const getTypes = () => {
		const inputTypes: ISelectItem[] = TypeItems;

		return inputTypes;
	};

	const getHierarchy = () => {
		const inputTypes: ISelectItem[] = HierarchyItems;

		return inputTypes;
	};

	const getEnrollmentPeriod = () => {
		const inputTypes: ISelectItem[] = EnrollmentPeriodItems;

		return inputTypes;
	};

	const getEffectivityCoverage = () => {
		const inputTypes: ISelectItem[] = EffectivityCoverageItems;

		return inputTypes;
	};

	const getAgeLimit = () => {
		const inputTypes: ISelectItem[] = EligibilityAgeLimitItems;

		return inputTypes;
	}

	const initDefaltValues = (civilStatus, relationship) => {
		console.log('TEST', civilStatus, relationship, type, hierarchy, 
		enrollmentPeriod, enrollmentPeriodUnit, ageLimitMin, ageLimitMinUnit, ageLimitMax, ageLimitMaxUnit);
		if (civilStatus === 'MARRIED' ||
			civilStatus === 'WIDOWED' ||
			civilStatus === 'SEPARATED' ||
			civilStatus === 'DIVORCED' ||
			civilStatus === 'MP') {
			if (relationship === 'Spouse') {
				setType('immediate');
				setHierarchy('1');
				setEnrollmentPeriod('30');
				setEnrollmentPeriodUnit('endorsement_date');
				setAgeLimitMin('18');
				setAgeLimitMinUnit('years');
				setAgeLimitMax('65');
				setAgeLimitMaxUnit('years');
			} else if (relationship === 'Child') {
				setType('immediate');
				setHierarchy('2');
				setEnrollmentPeriod('30');
				setEnrollmentPeriodUnit('endorsement_date');
				setAgeLimitMin('30');
				setAgeLimitMinUnit('days');
				setAgeLimitMax('21');
				setAgeLimitMaxUnit('years');
			}
		} else if (
			civilStatus === 'SINGLE' ||
			civilStatus === 'ANNULLED' ||
			civilStatus === 'UMP') {
			if (relationship === 'Mother') {
				setType('immediate');
				setHierarchy('1');
				setEnrollmentPeriod('30');
				setEnrollmentPeriodUnit('endorsement_date');
				setAgeLimitMin('18');
				setAgeLimitMinUnit('years');
				setAgeLimitMax('65');
				setAgeLimitMaxUnit('years');
			} else if (relationship === 'Father') {
				setType('immediate');
				setHierarchy('1');
				setEnrollmentPeriod('30');
				setEnrollmentPeriodUnit('endorsement_date');
				setAgeLimitMin('18');
				setAgeLimitMinUnit('years');
				setAgeLimitMax('65');
				setAgeLimitMaxUnit('years');
			} else if (relationship === 'Sibling') {
				setType('immediate');
				setHierarchy('2');
				setEnrollmentPeriod('30');
				setEnrollmentPeriodUnit('endorsement_date');
				setAgeLimitMin('30');
				setAgeLimitMinUnit('days');
				setAgeLimitMax('21');
				setAgeLimitMaxUnit('years');
			}
		} else if (civilStatus === 'SINGLEPARENT') {
			if (relationship === 'Child') {
				setType('immediate');
				setHierarchy('1');
				setEnrollmentPeriod('30');
				setEnrollmentPeriodUnit('endorsement_date');
				setAgeLimitMin('30');
				setAgeLimitMinUnit('days');
				setAgeLimitMax('21');
				setAgeLimitMaxUnit('years');
			} else if (relationship === 'Mother') {
				setType('immediate');
				setHierarchy('2');
				setEnrollmentPeriod('30');
				setEnrollmentPeriodUnit('endorsement_date');
				setAgeLimitMin('18');
				setAgeLimitMinUnit('years');
				setAgeLimitMax('65');
				setAgeLimitMaxUnit('years');
			} else if (relationship === 'Father') {
				setType('immediate');
				setHierarchy('2');
				setEnrollmentPeriod('30');
				setEnrollmentPeriodUnit('endorsement_date');
				setAgeLimitMin('18');
				setAgeLimitMinUnit('years');
				setAgeLimitMax('65');
				setAgeLimitMaxUnit('years');
			} else if (relationship === 'Sibling') {
				setType('immediate');
				setHierarchy('3');
				setEnrollmentPeriod('30');
				setEnrollmentPeriodUnit('endorsement_date');
				setAgeLimitMin('30');
				setAgeLimitMinUnit('days');
				setAgeLimitMax('21');
				setAgeLimitMaxUnit('years');
			}
		}
	}

	return (
		<Grid item sm={12} style={{padding: '10px'}}>
			<DialogTitle id="form-dialog-title">Add a Relationship</DialogTitle>
			<DialogContent>
				<Grid container spacing={2} justify="space-between" alignItems="flex-start">
					<Grid item sm={12}>
						<InputSelect
							id="civil-status-select"
							label="Civil Status"
							data={getCivilStatus()}
							value={civilStatus}
							error={civilStatusError}
							onChange={(selected: object) => {
								const value = selected['value'];
								setCivilStatus(value);
								setCivilStatusError(value.length === 0);
								initDefaltValues(value, relationship);
							}}
						/>
					</Grid>

					<Grid item sm={12}>
						<InputSelect
							id="relationship-select"
							label="Relationship to Principal"
							data={getRelationships()}
							value={relationship}
							error={relationshipError!==''}
							onChange={(selected: object) => {
								const label = selected['label'];
								const value = selected['value'];
								setRelationship(label);
								setRelationshipCode(value);
								setRelationshipError('');
								initDefaltValues(civilStatus, label);
							}}
						/>
					</Grid>

					<Grid item sm={8}>
						<InputSelect
							id="type-select"
							label="Type"
							controlled
							data={getTypes()}
							value={type}
							error={typeError}
							onChange={(selected: object) => {
								const value = selected['value'];
								setType(value);
								setTypeError(value.length === 0);
							}}
						/>
					</Grid>

					<Grid item sm={4}>
						<InputSelect
							id="hierarchy-select"
							label="Hierarchy"
							controlled
							data={getHierarchy()}
							value={hierarchy}
							error={hierarchyError}
							onChange={(selected: object) => {
								const value = selected['value'];
								console.log('hierarchy',selected)
								setHierarchy(value);
								if(Number(value) > 0){
									setHierarchyError(false);
								}
							}}
						/>
					</Grid>
					<Grid item sm={3}>
						<InputTextField
							id="enrollment-select"
							label="Enrollment Period (x days from)"
							value={enrollmentPeriod}
							error={enrollmentPeriodError}
							type={"tel"}
							onChange={(value: string) => {
								if(!numallowedpattern.test(value)){
									if(value.length===0)
									{
										value = '0';
									}
									else if(value.indexOf('0')===0 && value.length===1)
									{
										value = '0';
									}
									else if(value.indexOf('0')===0)
									{
										value = value.substring(1);
									}
									setEnrollmentPeriod(value);
									if(Number(value) >= 0){
										setEnrollmentPeriodError(false);
									}	
								}
							}}
						/>
					</Grid>
					<Grid item sm={9}>
						<InputSelect
							id="enrollment-unit-select"
							label="&nbsp;"
							controlled
							data={getEnrollmentPeriod()}
							value={enrollmentPeriodUnit}
							error={enrollmentPeriodUnitError}
							onChange={(selected: object) => {
								const value = selected['value'];
								setEnrollmentPeriodUnit(value);
								setEnrollmentPeriodUnitError(value.length === 0);
							}}
						/>
					</Grid>
					<Grid item sm={3}>
						<InputTextField
							id="effectivity-select"
							label="Effectivity Coverage (x days from)"
							value={effectivityCoverage}
							error={effectivityCoverageError}
							type={"tel"}
							onChange={(value: string) => {
								if(!numallowedpattern.test(value)){
									if(value.length===0)
									{
										value = '0';
									}
									else if(value.indexOf('0')===0 && value.length===1)
									{
										value = '0';
									}
									else if(value.indexOf('0')===0)
									{
										value = value.substring(1);
									}
									setEffectivityCoverage(value);
									if(Number(value) >= 0){
										setEffectivityCoverageError(false);
									}
								}
							}}
						/>
					</Grid>
					<Grid item sm={9}>
						<InputSelect
							id="effectivity-unit-select"
							label="&nbsp;"
							controlled
							data={getEffectivityCoverage()}
							value={effectivityCoverageUnit}
							error={effectivityCoverageUnitError}
							onChange={(selected: object) => {
								const value = selected['value'];
								setEffectivityCoverageUnit(value);
								setEffectivityCoverageUnitError(value.length === 0);
							}}
						/>
					</Grid>

					<Grid item sm={3}>
						<InputTextField
							id="age-limit-min-select"
							label="Age Limit (min)"
							value={ageLimitMin}
							error={ageLimitMinError}
							type={"tel"}
							onChange={(value: string) => {
								if(!numallowedpattern.test(value)){
									if(value.length===0)
									{
										value = '0';
									}
									else if(value.indexOf('0')===0 && value.length===1)
									{
										value = '0';
									}
									else if(value.indexOf('0')===0)
									{
										value = value.substring(1);
									}
									setAgeLimitMin(value);
									if(Number(value) > 0){
										setAgeLimitMinError(false);
									}
								}
							}}
						/>
					</Grid>
					<Grid item sm={3}>
						<InputSelect
							id="age-limit-min-unit-select"
							label="&nbsp;"
							controlled
							data={getAgeLimit()}
							value={ageLimitMinUnit}
							error={ageLimitMinUnitError}
							onChange={(selected: object) => {
								const value = selected['value'];
								setAgeLimitMinUnit(value);
								setAgeLimitMinUnitError(value.length === 0);
							}}
						/>
					</Grid>

					<Grid item sm={3}>
						<InputTextField
							id="age-limit-max-select"
							label="Age Limit (max)"
							value={ageLimitMax}
							error={ageLimitMaxError}
							type={"tel"}
							onChange={(value: string) => {
								if(!numallowedpattern.test(value)){
									if(value.length===0)
									{
										value = '0';
									}
									else if(value.indexOf('0')===0 && value.length===1)
									{
										value = '0';
									}
									else if(value.indexOf('0')===0)
									{
										value = value.substring(1);
									}
									setAgeLimitMax(value);
									if(Number(value) > 0){
										setAgeLimitMaxError(false);
									}
								}
							}}
						/>
					</Grid>
					<Grid item sm={3}>
						<InputSelect
							id="age-limit-max-unit-select"
							label="&nbsp;"
							controlled
							data={getAgeLimit()}
							value={ageLimitMaxUnit}
							error={ageLimitMaxUnitError}
							onChange={(selected: object) => {
								const value = selected['value'];
								setAgeLimitMaxUnit(value);
								setAgeLimitMaxUnitError(value.length === 0);
							}}
						/>
					</Grid>

					<Grid item sm={12}>
					</Grid>
				</Grid>
			</DialogContent>
			<DialogActions style={{paddingBottom: '50px'}}>
				<Grid container spacing={2} justify="center" alignItems="flex-start">
					<Grid item xs={5}>
						<Button 
							variant="outlined" 
							color="default" 
							fullWidth
							onClick={handleClose}
						>
							Cancel
						</Button>
					</Grid>
					<Grid item xs={5}>
						<Button
							variant="contained"
							color="primary"
							fullWidth
							onClick={handleSubmit}
						>
							Add
						</Button>
					</Grid>
				</Grid>
			</DialogActions>
		</Grid>
	);
}

EligibilityForm.defaultProps = defaultProps;