import React, { useState, useEffect } from 'react';

import {
  Button,
  Checkbox,
  Drawer,
  FormControlLabel,
  Grid,
  IconButton,
  Typography,
} from '@material-ui/core';
import { makeStyles, Theme } from '@material-ui/core/styles';
import CloseIcon from '@material-ui/icons/Close';

import { Components } from '@hims/core';

import { BasicTextField, BasicSelect } from 'Components/UI';

const InputChipTextField = Components.UI.InputChipTextField.InputChipTextField;

const addMemberFieldDrawerStyles = makeStyles((theme: Theme) => ({
  main: {
    width: 480,
    position: 'fixed',
    right: '0px',
    top: '0px',
    height: '100%',
    maxHeight:'100vh',
    overflowY:'auto',
    backgroundColor: '#F5F7FB',
    boxShadow: '0px 3px 6px #00000029',
    zIndex: 9990,
  },
  container: {
    padding: '60px 30px 30px 30px',
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
  drawerTitle: {
    fontFamily: 'Usual, Roboto, Helvetica, Arial, sans-serif',
    fontSize: '1.25rem',
    fontWeight: 550,
  },
  formContainer: {
    paddingTop: '20px',
  },
  checkbox: {
    '&.Mui-checked': {
      color: '#1e2071',
    },
  },
}));

interface AddMemberFieldDrawerProps {
  isOpen: boolean;
  invalidFields?:any[];
  onSubmit: (data: any) => void;
  onClose: () => void;
}

export const AddMemberFieldDrawer: React.FC<AddMemberFieldDrawerProps> = (
  props: AddMemberFieldDrawerProps,
): JSX.Element => {
  const classes = addMemberFieldDrawerStyles();

  const defaultSelectOption = { name: '', value: '' };

  const [inputTypes] = useState([
    defaultSelectOption,
    { name: 'Text Field', value: 'Text Field' },
    { name: 'Dropdown', value: 'Dropdown' },
  ]);

  const [inputDataTypes] = useState([
    defaultSelectOption,
    { name: 'Alphanumeric', value: 'Alphanumeric' },
    { name: 'Numeric', value: 'Numeric' },
    { name: 'Date', value: 'Date' },
  ]);

  const [fieldName, setFieldName] = useState('');
  const [fieldInputType, setFieldInputType] = useState('');
  const [fieldDataType, setFieldDataType] = useState('');
  const [fieldDropdownValues, setFieldDropdownValues] = useState<any[]>([]);
  const [fieldIsRequired, setFieldIsRequired] = useState(false);

  useEffect(() => {
    if (props.isOpen) {
      resetForm();
    }
  }, [props.isOpen]);

  const handleFieldNameChange = (
    event: React.ChangeEvent<HTMLInputElement>,
  ) => {
    setFieldName(event.target.value);
  };

  const handleFieldInputTypeChange = (
    event: React.ChangeEvent<HTMLInputElement>,
  ) => {
    setFieldInputType(event.target.value);
  };

  const handleFieldDataTypeChange = (
    event: React.ChangeEvent<HTMLInputElement>,
  ) => {
    setFieldDataType(event.target.value);
  };

  const handleFielIsRequiredChange = (
    event: React.ChangeEvent<HTMLInputElement>,
  ) => {
    setFieldIsRequired(event.target.checked);
  };

  const resetForm = () => {
    setFieldName('');
    setFieldInputType('');
    setFieldDataType('');
    setFieldDropdownValues([]);
    setFieldIsRequired(false);
  };

  const onClose = () => {
    if (props.onClose) {
      props.onClose();
    }
  };

  const onSubmit = () => {
    if (props.onSubmit) {
      const data = {
        fieldName,
        fieldInputType,
        fieldDataType,
        fieldDropdownValues,
        fieldIsRequired,
      };

      props.onSubmit(data);
    }
  };

  return (
    <Drawer
      open={props.isOpen}
      anchor="right"
      ModalProps={{
        hideBackdrop: true,
        keepMounted: true,
        open: false,
      }}
    >
      <div className={classes.main} role="presentation">
        <Grid container className={classes.container}>
          <Grid item xs={12}>
            <IconButton
              aria-label="close"
              className={classes.closeButton}
              onClick={onClose}
            >
              <CloseIcon />
            </IconButton>
          </Grid>
          <Grid item xs={12}>
            <Typography className={classes.drawerTitle}>
              Add a Custom Data Field
            </Typography>
            <Typography>You can add up to 5 custom data fields.</Typography>
          </Grid>
          <Grid item xs={12} className={classes.formContainer}>
            <BasicTextField
              id="addFieldName"
              label="Data Field Name"
              width="100%"
              InputProps={{
                type: 'text',
              }}
              error={props.invalidFields && props.invalidFields.includes('Empty field name')
                      && fieldName===''}
              value={fieldName}
              onChange={handleFieldNameChange}
            />
          </Grid>
          <Grid item xs={12} className={classes.formContainer}>
            <BasicSelect
              id="addFieldInputType"
              label="Input Type"
              menuItems={inputTypes}
              value={fieldInputType}
              error={props.invalidFields && props.invalidFields.includes('Input type not selected')
                      && fieldInputType===''}
              onChange={handleFieldInputTypeChange}
              width="100%"
            />
          </Grid>
          {fieldInputType === 'Text Field' ? (
            <Grid item xs={12} className={classes.formContainer}>
              <BasicSelect
                id="addFieldDataType"
                label="Data Type"
                menuItems={inputDataTypes}
                error={props.invalidFields && props.invalidFields.includes('Data type not selected')
                  && fieldDataType===''}
                value={fieldDataType}
                onChange={handleFieldDataTypeChange}
                width="100%"
              />
            </Grid>
          ) : null}

          {fieldInputType === 'Dropdown' ? (
            <Grid item xs={12} className={classes.formContainer}>
              <InputChipTextField
                label="Dropdown Options"
                error={props.invalidFields && props.invalidFields.includes('No dropdown options')
                        && fieldDropdownValues.length===0}
                onChange={(options: any[]) => {
                  setFieldDropdownValues(options);
                }}
              />
            </Grid>
          ) : null}

          <Grid item xs={12} className={classes.formContainer}>
            <FormControlLabel
              control={
                <Checkbox
                  color="primary"
                  classes={{
                    colorPrimary: classes.checkbox,
                  }}
                  checked={fieldIsRequired}
                  onChange={handleFielIsRequiredChange}
                />
              }
              label="Required?"
            />
          </Grid>
          <Grid item xs={12} className={classes.formContainer}>
            <Grid
              container
              spacing={2}
              justify="space-around"
              alignItems="flex-start"
            >
              <Grid item xs={4}>
                <Button
                  onClick={onClose}
                  variant="outlined"
                  color="default"
                  fullWidth
                >
                  Cancel
                </Button>
              </Grid>
              <Grid item xs={4}>
                <Button
                  onClick={onSubmit}
                  variant="contained"
                  color="primary"
                  fullWidth
                >
                  Add Field
                </Button>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </div>
    </Drawer>
  );
};
