export const ClientBenefitsPage: React.FC<IClientBenefitsPage.IProps> = (props: IClientBenefitsPage.IProps): JSX.Element => {
    
    const [isLoading, setIsLoading] = useState(false);
    
    const classes = viewClientStyles();

    const [contractName, setContractName] = useState<string>('');
    const [registeredName, setRegisteredName] = useState('');
    const [corporateAccNo, setCorporateAccNo] = useState('');
    const [effectivityDate, setEffectivityDate] = useState<string>('');
    const [expiryDate, setExpiryDate] = useState<string>('');

    const [clientLoaded, setClientLoaded] = useState(false);
    const [nodesLoaded, setNodesLoaded] = useState(false);

    const { 
        clientData, 
        SetBenefitNodes, 
        SetClientData, 
        exclusionList, 
        SetExclusionList,
        SetSelectedPlanType,
        SetSelectedPlanPackage,
        SetSelectedBenefitCategory
    } = props;

    const isValidDate = (date: string) => {
        const d = new Date(date);
        return d instanceof Date && !isNaN(d.getTime());
    }

    const handleLoadClientFromAPI = () => {
        API.getClientHMOData(props.match.params['client_id'])
        .then(response => {
            if(response && response.error === undefined){
                console.log('get client hmo info', response); 
                const data: any = Object.assign({}, (response.client ? response.client : {}));

                const client = convertClientData(data);

                SetClientData(Object.assign({}, clientData, client));

                setCorporateAccNo(response.client && response.client.corporate_account_no ? response.client.corporate_account_no : '');
                setRegisteredName(response.client && response.client.registered_name ? response.client.registered_name : '');

                let benefitTree: any[] = response.benefit_plan_tree ? response.benefit_plan_tree : [];

                let ncontractName: string = '';
                let neffectivityDate: string = '';
                let nexpiryDate: string = '';

                if(benefitTree.length > 0) {
                    benefitTree.forEach(item => {
                        if(item.level === 2 && item.tree_id === 'MCT'){
                            ncontractName = item.name ? item.name : '';
                            if(item.custom_metadata){
                                neffectivityDate = item.custom_metadata.coverage_start_date ? item.custom_metadata.coverage_start_date : '';
                                nexpiryDate = item.custom_metadata.coverage_end_date ? item.custom_metadata.coverage_end_date : '';
                            }
                        }
                    });
                }

                setContractName(ncontractName);
                setEffectivityDate(neffectivityDate);
                setExpiryDate(nexpiryDate);

                let ptCodes: string[] = [];
                if(response.plan_types && response.plan_types.length > 0){
                    ptCodes = response.plan_types.map(item => item.plan_type_code);
                }

                let propsJson = JSON.parse(JSON.stringify(props));
                let queryValues = queryStringToJSON(
                    propsJson.location.search,
                );
                let plantype = queryValues && queryValues.plantype && ptCodes.indexOf(queryValues.plantype) > -1 ? queryValues.plantype : 'BASE';
                SetSelectedPlanType(plantype);
                
                setClientLoaded(true);
            }
            else{
                console.log('getclient hmo api error: ', response);
                setClientLoaded(true);
            }
        })
        .catch(error => {
            console.log('client hmo error: ', error);
            setClientLoaded(true);
        });
    }

    const handleLoadBaseNodesFromAPI = () => {
        API.getBaseBenefits().then(response => {
            if(response && response.error === undefined){
                const data: any[] = [...response];
                let nodes : any[] = [];

                nodes = convertNodeData(data);
                    
                handleLoadBenefitNodesFromAPI(nodes);
            }else{
                setIsLoading(false);
                console.log('getbenefitlist api error: ', response);
            }
        });
    }

    const handleLoadBenefitNodesFromAPI = (baseNodesParam: any[]) => {
        API.getContractTree(props.match.params['contract_id']).then(response => {
            if(response && response.length > 0 && response.error === undefined){
                const data: any[] = [...response];
                let baseNodes: any [] = [...baseNodesParam];
                let benefitNodes : any[] = [];
                let eList : any[] = [...exclusionList];

                benefitNodes = convertNodeData(data, true);
                //console.log('benefit nodes: ',benefitNodes)
                let nodes: any[] = combineBaseBenefitNodes(baseNodes, benefitNodes, true);

                eList = createExclusionList(nodes);
                
                setNodesLoaded(true);
                SetExclusionList(eList);
                SetSelectedPlanPackage('RB');
                SetSelectedBenefitCategory('RBC');
                SetBenefitNodes (nodes);
            }
            else{
                setNodesLoaded(true);
                console.log('getbenefitlist api error: ', response);
            }
        }).catch(error => {
            setNodesLoaded(true);
            console.log('getbenefitlist api error: ', error);
        });
    }

    const queryStringToJSON = queryString => {
        if (queryString.trim().length < 1) {
          return {};
        }
    
        var pairs = queryString.slice(1).split('&');
    
        var result = {};
        pairs.forEach(function(pair) {
          pair = (pair as string).split('=');
          result[pair[0]] = decodeURIComponent(pair[1] || '');
        });
    
        return JSON.parse(JSON.stringify(result));
    };
    
    let breadcrumbs_items = [
        {
            label: 'Clients',
            url: '#/underwriting/clients',
        },
        {
            label: registeredName ? registeredName : 'N.A.',
            url: '#/underwriting/client/' + props.match.params['client_id'],
        },
        {
          label: 'History',
          url: '',
        },
        {
            label: contractName,
            url: '',
        },
    ];

    useEffect(() => {
        setIsLoading(true);
        handleLoadClientFromAPI();
    }, []);

    useEffect(() => {
        if(clientLoaded)
        {
            handleLoadBaseNodesFromAPI();
        }
        //handleLoadBaseNodesFromAPI();
        // handleLoadBenefitNodesFromAPI();
    }, [clientLoaded]);

    useEffect(() => {
        if (clientLoaded && nodesLoaded) {
            setIsLoading(false);
        }
    }, [nodesLoaded]);

    return (
        <div className={classes.main}>
            { isLoading ?
                (<LoadingIndicator/>) : null
            }
            <Grid container justify="space-between" alignItems="center" className={classes.gridHeader}>
                <Grid item xs={12}>
                    <Components.UI.BreadcrumbsComponent
                        items={breadcrumbs_items}
                    />
                    <Typography className={classes.headerTitle} style={{paddingTop: 8}}>
                        { registeredName }
                    </Typography>
                </Grid>
            </Grid>
            <Grid container justify="space-between" alignItems="center" className={classes.gridContainer} >
                <Grid item xs={12} md={10}>
                    <Grid container>
                        <Grid item xs={12} md={3}>
                            <HMOTextItem
                                name="Corporate Account No."
                                value={corporateAccNo ? corporateAccNo : '-'} />
                        </Grid>
                        <Grid item xs={12} md={3}>
                            <HMOTextItem
                                name="Contract"
                                value={contractName ? contractName : '-'} />
                        </Grid>
                        <Grid item xs={12} md={3}>
                            <HMOTextItem
                                name="Effectivity Date"
                                value={
                                    effectivityDate && isValidDate(effectivityDate)
                                    ? moment(effectivityDate).format('MMM D, YYYY')
                                    : '-'
                                } />
                        </Grid>
                        <Grid item xs={12} md={3}>
                            <HMOTextItem
                                name="Expiry Date"
                                value={
                                    expiryDate && isValidDate(expiryDate)
                                    ? moment(expiryDate).format('MMM D, YYYY')
                                    : '-'
                                } />
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item xs={12}>
                    <CBPlanTypes 
                        viewOnly 
                        classes={classes} 
                        {...props}
                        SetNewNodeCounter={() => console.log("SetNewNodeCounter")}
                        newNodeCounter={0} />
                </Grid>
            </Grid>
        </div>
    );
};

const mapStateToProps = (state: Store) => {
    return {
        benefitNodes: getBenefitNodes(state),
        clientData: getClientData(state),
        contractNode: selectContractNode(state),
        categoryNode: selectCategoryNode(state),
        planTypeList: selectPlanTypeList(state),
        roomAndBoardList: selectRoomAndBoardList(state),
        coverageList: selectCoverageList(state),
        riderList: selectRiderList(state),
        rows: selectRows(state),
        selectedPlanType: getSelectedPlanType(state),
        selectedPlanPackage: getSelectedPlanPackage(state),
        selectedBenefitCategory: getSelectedBenefitCategory(state),
        businessData: selectBusinessData (state),
        natureData: selectNatureData (state),
        contactDetailsData: selectContactDetailsData (state),
        HMOData: selectHMOData (state),
        principalData: selectPrincipalData(state),
        exclusionList: getExclusionList(state),
    }
};

const mapDispatchToProps = (dispatch: Dispatch) => ({
    SetBenefitNodes: bindActionCreators(ClientBenefitsActions.SetBenefitNodes, dispatch),
    SetClientData: bindActionCreators(ClientBenefitsActions.SetClientData, dispatch),
    SetSelectedPlanType: bindActionCreators(ClientBenefitsActions.SetSelectedPlanType, dispatch),
    SetSelectedPlanPackage: bindActionCreators(ClientBenefitsActions.SetSelectedPlanPackage, dispatch),
    SetSelectedBenefitCategory: bindActionCreators(ClientBenefitsActions.SetSelectedBenefitCategory, dispatch),
    SetExclusionList: bindActionCreators(ClientBenefitsActions.SetExclusionList, dispatch),
});

export { mapStateToProps, mapDispatchToProps };
